.bannerBox {
    padding: 100px 0;
    position: relative;
    z-index: 1;
}

.container{
    max-width: 1200px;
    margin: 0 auto;
}

.socialCont__conts{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center
}

.bannerBox::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, #21222966, #1a1b2066), url('https://res.cloudinary.com/dfasgvfex/image/upload/v1712075741/web-banners/aboutbanner.jpg');
    filter: blur(5px);
    height: 400px;
    z-index: -1;
    background-size: cover;
}

.bannerContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.bannerImg {
    max-width: 350px;
    
}

.bannerTitleBox {
    margin-bottom: 20px;
    text-align: center;
}

.bannerTitleBox>h2 {
    font-size: 42px;
    color: #fff;
    font-weight: bold;
    text-align: center;
}

.bannerTitleBox>span {
    font-size: 22px;
    color: #fff;
}

.socialCont{
    padding-bottom: 30px;
}

.socialImg{
    max-width: 80px;
    margin-right: 30px;
}

.socialLink{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #000;
}

h3{
    text-align: center;
    font-size: 32px;
}

.aboutSub{
    font-size: 22px;
}

.container{
    padding: 0 15px;
}

@media screen and (min-width:1200px) {

    .bannerContent {
        flex-direction: row;
    }
    .bannerBox::before {
        height: 370px;
    }
    .bannerImg {
        max-width: 550px;
    }

    .bannerTitleBox>h2 {
        text-align: start;
    }

    .bannerTitleBox {
        margin-right: 50px;
        text-align: start;
    }
}

@media screen and (min-width:1650px) {

    .bannerContent {
        flex-direction: row;
    }

    .bannerBox::before {
        height: 470px;
    }

    .bannerImg {
        max-width: 750px;
    }

    .bannerTitleBox>h2 {
        text-align: start;
    }
}